.btnGoBack {
  display: flex;
  width: 110px;
  height: 20px;
  padding: 10px;
  background-color: rgb(255, 251, 0);
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  border: 5px inset #0059ff;
  border-radius: 0px 40px 0px 40px;
  margin-top: 10px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.btnGoBack:hover {
  box-shadow: 4px -4px 15px 0px #ff1f1f, 12px -11px 7px 0px #ff9376,
    20px -5px 7px 0px #ffe264, 20px 6px 7px 0px #f6ff33,
    13px 12px 17px 0px #ff9527, 2px 17px 17px 0px #ff0000,
    -9px 21px 18px 0px #fff212, -9px 6px 11px 0px #ff0808,
    -11px -9px 11px 0px #fffa17, -11px -9px 11px 0px #fffa17,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
}

.section {
  display: flex;
  padding: 30px 0;
  font-size: 18px;
}

.infoContainer {
  margin-left: 20px;
  padding: 10px;
  text-align: center;
}

ul {
  margin-bottom: 10px;
  list-style-type: '\1F44D';
}
